@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;800&display=swap);
body {
  font-weight: 300;
  font-family: 'Montserrat', sans-serif;
}

h1, h2, h3, h4, strong {
  font-weight: 400;
}

.signup-text {
  text-align: start;
  font-size: 1.3em;
}

.login-text {
  text-align: start;
  font-size: 1.3em;
}

.toast {
  position: fixed;
  bottom: 10px;
  right: 10px;
  z-index: 99999;
  width: 350px
}

.error-message {
  margin: 10px 0;
  background: red;
  color: white;
  padding: 10px;
  border-radius: 5px;
  font-size: 1em;
  font-weight: 500;
}
.restaurants-list .card{   
    margin-bottom: 30px;
    min-height: 430px;
}

.restaurants-list img {
    height: 180px;
    object-fit: cover;
}

h3 {
    font-size: 1.5em;
    font-weight: 500;
}

h5 {
    font-weight: 600;
    font-size: 1.3em;
}

.btn {
    font-weight: 500;
    font-size: .9em;
}

.btn-choose img {
    width: 25px;
    margin: 0 2px;
  }

  .btn-choose {
    align-items: center;
    font-weight: 500;
    font-size: 1.6em;
    padding: 10px 40px;
    margin: 50px 0 50px 0;
  }
 /* --- RESTAURANT DETAIL */
.restaurant-info {
    padding-top: 20px;
}

 .title-card {
    font-size: 2em;
    font-weight: 600;

 }

.text-card {
    font-size: 1.3em;
}

 /* --- REVIEW CARD */

.comment-title {
    margin: 10px 0;
    text-align: center;
    font-weight: 500;
    padding: 15px;
}

h5 {
    text-align: right;
    font-weight: 500;
    margin: 10px 0;
}

.img-rating {
    width: 20px;
    margin: 0 3px 3px 5px;
}

.comment {
    margin-bottom: 10px;
}

.avatar {
    border-radius: 50%;
    width: 60px;
    margin: 10px;
}

h6 {
    font-size: 1.4em;
}

.rating-info {
    font-size: 1.15em;
    font-weight: 500;
    text-align: end;
    margin-bottom: 20px;
}

.btn-back {
    margin-bottom: 20px;
}
a {
    color:black;
    text-decoration: none;
    font-weight: 500;
  }
  
  .navbar a:hover {
    font-size: 1.15em;
    transition: .3s;
    font-weight: 500;
    color: #1a6b9f;
    text-decoration: none;
  }

  .navbar {
    margin-bottom: 40px;
  }
  
  .logo-navbar:hover {
    transition: 1s;
    -webkit-transition: 1s;
    -moz-transition: 1s;
    -ms-transition: 1s;
    -o-transition: 1s;
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
}
.home {
  padding: 100px;
}

.text-home {
  font-size: 4em;
  font-weight: 600;
  color: #1a6b9f;
  text-align: center;
  }
  
  .img-logo {
    width: 45px;
  }

  .img-logo-btn {
    width: 45px;
  }  

  p {
    font-weight: 400;
    font-size: 1.6em;
    text-align: center;
  }





